import { chainConfigs, valueToKeyMap } from "config/constants/tokenConfig";
import {SupportedChainId} from "../config/constants/chains";



export default async function addAsset({ connector, tokenSymbol, chainId }: { connector: any
  tokenSymbol: string,

  chainId: SupportedChainId}){
    console.log('object :>> ', chainConfigs[chainId].tokens[valueToKeyMap[tokenSymbol]].address);
  try {
    const provider = await connector.getProvider()
    await provider.request({
        method: 'wallet_watchAsset',
        params: {
          type: 'ERC20', // Initially only supports ERC20, but eventually more!
          options: {
            address: chainConfigs[chainId].tokens[valueToKeyMap[tokenSymbol]].address, // The address that the token is at.
            symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
            decimals: chainConfigs[chainId].tokens[valueToKeyMap[tokenSymbol]].decimals, // The number of decimals in the token
          },
        },
      });
  } catch (error) {
    //
  }
}
