const { faucet } = contracts

// Inspired by blueprintjs
export function BpCheckbox(props: CheckboxProps) {
  return (
    <CheckboxWrap
      sx={{
        '&:hover': { bgcolor: 'transparent' },
      }}
      disableRipple
      color="default"
      checkedIcon={<BpCheckedIcon />}
      icon={<BpIcon />}
      inputProps={{ 'aria-label': 'Checkbox demo' }}
      {...props}
    />
  )
}

export default function Faucet() {
  const { account, connector, chainId, active } = useWeb3React()
  const { signer } = useSystemParams()
  const [faucetContract, setFacuetContract] = useState(null)
  const [limitedMapping, setLimitDays] = useState(null)
  const [isAirdrop, setIsAirdrop] = useState(true)
  const [airDropToken, setAirDropToken] = useState({ WBTC: true, USDT: true, WETH: true })
  const { toastSuccess, toastError, toastInfo } = useToast()

  const addToMetaMask = useCallback(
    (symbol) => {
      if (active && chainId === SupportedChainId.BLAST_SEPPLIA) {
        addAsset({ connector, tokenSymbol: symbol, chainId })
      }
    },
    [active, chainId, connector],
  )

  useEffect(() => {
    console.log(tokens.WBTC.decimals, ' tokens tokens tokens')
    let timer
    if (signer) {
      timer = setTimeout(() => {
        if (window.grecaptcha) {
          window.grecaptcha.enterprise.render('html_element', {
            sitekey: process.env.REACT_APP_reCAPTCHA_keys,
            hl: 'en',
            callback(res) {
              if (res) {
                setIsAirdrop(false)
              }
            },
          })
        }
      }, 2000)
      const contract = getContract(signer, faucet.abi, faucet.address[chainId])
      if (contract) {
        const _limitDay = [
          contract.limitedMapping(chainConfigs[chainId].tokens.WBTC.address),
          contract.limitedMapping(chainConfigs[chainId].tokens.USDT.address),
          contract.limitedMapping(chainConfigs[chainId].tokens.WETH.address),
        ]
        Promise.all(_limitDay).then((data) => {
          setLimitDays({
            WBTC: data[0],
            USDT: data[1],
            WETH: data[2],
          })
        })
      }
      setFacuetContract(contract)
    }
    return () => {
      clearTimeout(timer)
    }
  }, [chainId, signer])

  const handleAirdrop = useCallback(async () => {
    if (faucetContract) {
      const info = []
      setIsAirdrop(true)
      if (airDropToken.WBTC) {
        info.push(faucetContract.airdrop(chainConfigs[chainId].tokens.WBTC.address))
      }
      if (airDropToken.USDT) {
        info.push(faucetContract.airdrop(chainConfigs[chainId].tokens.USDT.address))
      }
      if (airDropToken.WETH) {
        info.push(faucetContract.airdrop(chainConfigs[chainId].tokens.WETH.address))
      }
      try {
        const [wbtcTx, usdtTx, wethTx] = await Promise.all(info)
        if (wbtcTx) {
          toastInfo(
            ToastTitle.submited,
            <TransactionSubmittedInfo
              txHash={wbtcTx.hash}
              content="Your WBTC airdrop request has been submitted successfully. The distribution will begin shortly."
            />,
          )
          const result = await wbtcTx.wait()
          if (result) {
            toastSuccess(ToastTitle.success, 'WBTC airdrop successful. Check the transaction history for detailed information.')
          }
        }
        if (usdtTx) {
          toastInfo(
            ToastTitle.submited,
            <TransactionSubmittedInfo
              txHash={usdtTx.hash}
              content="Your USDT airdrop request has been submitted successfully. The distribution will begin shortly."
            />,
          )
          const result = await usdtTx.wait()
          if (result) {
            toastSuccess(ToastTitle.success, 'USDT airdrop successful. Check the transaction history for detailed information.')
          }
        }
        if (wethTx) {
          toastInfo(
            ToastTitle.submited,
            <TransactionSubmittedInfo
              txHash={wethTx.hash}
              content="Your WETH airdrop request has been submitted successfully. The distribution will begin shortly."
            />,
          )
          const result = await wethTx.wait()
          if (result) {
            toastSuccess(ToastTitle.success, 'WETH airdrop successful. Check the transaction history for detailed information.')
          }
        }
      } catch (e: any) {
        setIsAirdrop(false)
        toastError(ToastTitle.failed, e?.reason || 'An error occurred while processing your request.')
      }
    }
  }, [airDropToken, chainId, faucetContract, toastError, toastInfo, toastSuccess])

  return (
    <>
      <Title>Faucet</Title>
      <SubTitle>You can claim the test tokens from the faucet once every 24 hours.</SubTitle>
      <div style={{ width: '100%', overflow: 'scroll' }}>
        <Wrapper>
          <Grid container>
            <Grid item xs={8}>
              <CardWrap>
                <h3>Wallet address</h3>
                <Input value={account} disabled type="text" />
                <h3>Request tokens</h3>
                <Grid container spacing={4}>
                  <Grid item xs={6}>
                    <TokenWrap>
                      <FormControlLabel
                        control={
                          <BpCheckbox
                            checked={airDropToken.WBTC}
                            onChange={(e, val) => {
                              setAirDropToken((state) => ({ ...state, WBTC: val }))
                            }}
                          />
                        }
                        label="test WBTC"
                        labelPlacement="end"
                      />
                      <Input
                        type="number"
                        disabled
                        value={limitedMapping ? formatWBTC(limitedMapping.WBTC) : 0}
                        dir="rtl"
                      />
                    </TokenWrap>
                  </Grid>
                  <Grid item xs={6}>
                    <TokenWrap>
                      <FormControlLabel
                        control={
                          <BpCheckbox
                            checked={airDropToken.USDT}
                            onChange={(e, val) => {
                              setAirDropToken((state) => ({ ...state, USDT: val }))
                            }}
                          />
                        }
                        label="test USDT"
                        labelPlacement="end"
                      />
                      <Input
                        type="number"
                        disabled
                        value={
                          limitedMapping
                            ? Number(limitedMapping.USDT as any) / (toSmallUnits('1', tokens.USDT.decimals) as any)
                            : 0
                        }
                        dir="rtl"
                      />
                    </TokenWrap>
                  </Grid>
                </Grid>
                <Grid container spacing={4} className="addToWallet">
                  <Grid item xs={6}>
                    <ButtonText
                      className="btn"
                      onClick={() => {
                        addToMetaMask('WBTC')
                      }}
                    >
                      add to MetaMask
                    </ButtonText>
                  </Grid>
                  <Grid item xs={6}>
                    <ButtonText
                      className="btn"
                      onClick={() => {
                        addToMetaMask('USDT')
                      }}
                    >
                      add to MetaMask
                    </ButtonText>
                  </Grid>
                </Grid>
                <Grid container spacing={4}>
                  <Grid item xs={6}>
                    <TokenWrap>
                      <FormControlLabel
                        control={
                          <BpCheckbox
                            checked={airDropToken.WETH}
                            onChange={(e, val) => {
                              setAirDropToken((state) => ({ ...state, WETH: val }))
                            }}
                          />
                        }
                        label="test WETH"
                        labelPlacement="end"
                      />
                      <Input
                        type="number"
                        disabled
                        value={limitedMapping ? toLargeUnits(limitedMapping.WETH, commonTokens.WETH.decimals) : 0}
                        dir="rtl"
                      />
                    </TokenWrap>
                  </Grid>
                </Grid>
                <Grid container spacing={4} className="addToWallet">
                  <Grid item xs={6}>
                    <ButtonText
                      className="btn"
                      onClick={() => {
                        addToMetaMask('WETH')
                      }}
                    >
                      add to MetaMask
                    </ButtonText>
                  </Grid>
                </Grid>
                <div id="html_element" />
                <ButtonWrap disabled={isAirdrop} onClick={handleAirdrop}>
                  Send Request
                </ButtonWrap>

                {/* <p className="link">
                  <a target="_blank" href="https://faucetlink.to/goerli" className="link" rel="noreferrer">
                    Request Ethereum Goerli testnet ETH from a faucet &nbsp;&nbsp;
                    <ArrowLine />
                  </a>
                </p>
                <p style={{ marginTop: 15 }} className="link">
                  <a target="_self" href="#/quickMode" className="link" rel="noreferrer">
                    Go to Quick Mode &nbsp;&nbsp;
                    <ArrowLine />
                  </a>
                </p> */}
              </CardWrap>
            </Grid>
          </Grid>
        </Wrapper>
      </div>
    </>
  )
}
